.profile__name {
  color: #fff;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin: 0 0 9px 0;
  padding: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 900px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    margin-bottom: 8px;
  }
}
